let legal = /^[\u4e00-\u9fa5A-Za-z0-9_-]+[\)）（，；\(\.\%。\u4e00-\u9fa5A-Za-z0-9_-]?$/
export default function subWords() {
    let word = prompt("输入需要被翻译的词条")
    if (word == null || word.match(/\S/) == null || !legal.test(word)) {
        alert("词条不合法！")
        return
    }
    $.ajax({
        url: "//localhost/api/addWords",
        type: "POST",
        timeout: 1000,
        data: { word: word },
        dataType: "JSON",
        success: function (response) {
            if (response.info == 1) {
                alert("添加成功！")
            } else {
                alert("添加失败！")
            }
        },
        error: function () {
            alert("添加失败！")
        }
    })
}