let legal = /^[\u4e00-\u9fa5A-Za-z0-9_-]+[\)）（，；\(\.\%。\u4e00-\u9fa5A-Za-z0-9_-]?$/
export default function submitTranslation(text) {
    let content = prompt("请输入释义")
    if (content == null || content.match(/\S/) == null || !legal.test(content)) {
        alert("空值或者含有非法字符！")
    } else {
        // 查询词条
        $.ajax({
            url: "//localhost/api/queryWord",
            type: "POST",
            timeout: 1000,
            data: { word: text },
            dataType: "JSON",
            success: function (response) {
                if (response.info == 1) {
                    // 若有则直接追加翻译
                    $.ajax({
                        url: "//localhost/api/submitTranslationsToTemp",
                        type: "POST",
                        timeout: 1000,
                        data: { word: text, translation: content },
                        dataType: "JSON",
                        success: function (response) {
                            console.log(response);
                            if (response.info == 1) {
                                alert("提交成功！")
                            } else {
                                alert("提交错误！")
                            }
                        },
                        error: function () {
                            alert("提交失败！")
                        },
                    });
                } else {
                    // 若没有则添加词条
                    $.ajax({
                        url: "//localhost/api/addWords",
                        type: "POST",
                        timeout: 1000,
                        data: { word: text },
                        dataType: "JSON",
                        success: function (response) {
                            if (response.info == 1) {
                                // 添加完之后追加翻译
                                $.ajax({
                                    url: "//localhost/api/submitTranslationsToTemp",
                                    type: "POST",
                                    timeout: 1000,
                                    data: { word: text, translation: content },
                                    dataType: "JSON",
                                    success: function (response) {
                                        console.log(response);
                                        if (response.info == 1) {
                                            alert("提交成功！")
                                        } else {
                                            alert("提交错误！")
                                        }
                                    },
                                    error: function () {
                                        alert("提交错误！")
                                    },
                                });
                            }
                        },
                        error: function () {
                            alert("添加词条失败！")
                        },
                    });
                }
            },
            error: function () {
                alert("查询时发生错误！")
            },
        });
        // $.ajax({
        //     url: "//localhost/api/submitTranslationsToTemp",
        //     type: "POST",
        //     timeout: 1000,
        //     data: { word: text, translation: content },
        //     dataType: "JSON",
        //     success: function (response) {
        //         console.log(response);
        //         alert("提交成功！")
        //     },
        //     error: function () {
        //         alert("发生错误！")
        //     },
        // });
    }
}